.FooterUp {
  display: flex;
  justify-content: space-between;
}

.Line {
  width: 635.003px;
  height: 3px;
  background: #037e8c;
}

.FooterUp h2 {
  margin-top: 14px;
  margin-bottom: 70px;
}

.Info {
  display: flex;
  gap: 15px;
}

.FooterUp h5 {
  color: #121214;
  font-size: 18px;
  font-weight: 400;
  line-height: 151.187%; /* 27.214px */
}

.SmallInfo {
  display: flex;
  gap: 16px;
  margin-bottom: 42px;
}

.Icons:hover {
  background-color: #10a4b4;
}

.IconHome {
  position: relative;
  background-color: #037e8c;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  transition: 0.5s;
}

.IconHome::after {
  position: absolute;
  content: '';
  top: 50%;
  left: 50%;
  width: 23px;
  height: 23px;
  background: url(./Images/home.png) 100% no-repeat;
  transform: translate(-50%, -50%);
}

