.RouteBlock {
  display: flex;
  gap: 52px;
}

.RouteBlockReverce {
  display: flex;
  flex-direction: row-reverse;
  margin-top: 144px;
  margin-bottom: 155px;
  gap: 55px;
}

.RouteBlockInfo {
  display: flex;
  justify-content: space-between;
  gap: 30px;
}

.Line {
  width: 81.006px;
  height: 4px;
  background: #037e8c;
  margin-top: 20px;
}

.UnderHeader {
  font-size: 24px;
  font-weight: 700;
  line-height: 108.7%; /* 26.088px */
  margin-top: 9px;
  margin-bottom: 22px;
}

.ExpreteSpan {
  font-weight: 600;
}

.Icons {
  margin-right: 17px;
}

.RouteBlockSmallInfo {
  display: flex;
  align-items: center;
  font-size: 16px;
  font-weight: 400;
  line-height: 108.7%; /* 17.392px */
  text-transform: uppercase;
  margin-top: 10px;
}

.RouteH4 {
  font-size: 18px;
  font-weight: 600;
  line-height: 130%; /* 23.4px */
  text-transform: uppercase;
  margin-top: 32px;
  margin-right: 15px;
}

.RouteText {
  width: 524px;
  margin-top: 13px;
}

.Btn {
  display: flex;
}

.Btn button {
  font-size: 14px;
  font-weight: 600px;
  line-height: 130%;
  margin-top: 41px;
}

.DefultButton {
  width: 175px;
  height: 45px;
  border-radius: 9px;
  border: 1.5px solid #037e8c;
  color: #037e8c;
  font-size: 14px;
  font-weight: 600px;
  line-height: 130%;
  margin-right: 30px;
}

.ExpriceButton {
  width: 175px;
  height: 45px;
  border-radius: 9px;
  background: #037e8c;
  color: #fff;
  border: none;
}

.RouteBlockImages {
  display: flex;
  gap: 30px;
}

.RouteBlockImagesReverce {
  display: flex;
  flex-direction: row-reverse;
  gap: 30px;
}
.byke {
  border-radius: 9px;
}

.TallCalage {
  display: flex;
  gap: 30px;
}
.TallCalageReverce {
  display: flex;
  flex-direction: row-reverse;
  gap: 30px;
}
