.Modal {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(18, 18, 20, 0.8);
  overflow: auto;
  color: #121214;
}

.modalIcons {
  color: #ffffff;
  font-size: 25px;
  margin-left: 1275px;
  margin-bottom: 40px;
}

.modalContent {
  display: flex;
  justify-content: center;
  width: 1300px;
  height: 479px;
  border-radius: 9px;
  background: #fff;
}

.Modal h4 {
  margin-top: 30px;
  margin-bottom: 27px;
  color: #121214;
  font-size: 24px;
  font-weight: 600;
}

.Modal p {
  width: 1078px;
  height: 249px;
  color: #121214;
  font-size: 16px;
  font-weight: 400;
  line-height: 160%; /* 25.6px */
}
