.FooterUnder {
  margin-top: 74px;
}

.Line {
  width: 1301px;
  height: 1px;
  background: #121214;
}

.FooterUnderContent {
  display: flex;
  justify-content: space-between;
}

.FooterUnder img {
  margin-left: 110px;
}

.FooterUnder ul {
  display: flex;
  gap: 50px;
  list-style: none;
  margin-top: 39px;
}

.FooterUnder li {
  color: #121214;
  font-size: 14px;
  font-weight: 600;
  line-height: 104.7%; /* 14.658px */
  text-transform: uppercase;
}

.FooterUnder p {
  color: #787878;
  font-size: 16px;
  font-weight: 400;
  line-height: 126.687%; /* 20.27px */
  text-align: center;
  margin-top: 21px;
  margin-bottom: 24px;
}
