.Menu {
  display: flex;
  justify-content: space-between;
  font-weight: 600;
}

.Menu ul {
  display: flex;
  gap: 50px;
  list-style: none;
  margin-top: 39px;
}

.Menu li {
  font-size: 14px;
  line-height: 104.7%; /* 14.658px */
  text-transform: uppercase;
}

.Special {
  color: #037e8c;
}

.MenuPhone {
  margin-top: 39px;
}

.Menu b {
  display: flex;
  font-size: 20px;
  line-height: 104.7%; /* 20.94px */
  letter-spacing: 0.4px;
  gap: 13px;
}

.Menu button {
  background: rgba(255, 255, 255, 0.4);
  line-height: 104.7%; /* 14.658px */
  letter-spacing: 0.28px;
  border-radius: 9px;
  width: 177px;
  height: 40px;
  font-size: 14px;
  color: #fff;
  margin-top: 11px;
  border: none;
  margin-left: 35px;
}
