.HeadingContent {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 108px;
}
.HeadingContent h1 {
  font-size: 70px;
  font-weight: 800;
  line-height: 125.5%; /* 87.85px */
  letter-spacing: 3.15px;
}
.HeadingContent h3 {
  font-size: 24px;
  font-weight: 500;
  line-height: 108.7%; /* 26.088px */
  margin-top: 37px;
}
.HeadingContent button {
  display: flex;
  align-items: center;
  padding-left: 23px;
  padding-right: 12px;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  line-height: 108.7%; /* 17.392px */
  width: 274px;
  height: 55px;
  border-radius: 9px;
  background: #037e8c;
  border: none;
  margin-top: 82px;
}

.HeadingContentArrow {
  margin-left: 32px;
}
