.activedModal {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(18, 18, 20, 0.8);
  color: #121214;
}

.disableModal {
  display: none;
}

.modalIcons {
  color: #ffffff;
  font-size: 25px;
  margin-bottom: 605px;
  padding-left: 84px;
}

.modalContent {
  display: flex;
  justify-content: center;
  width: 1074px;
  height: 479px;
  border-radius: 9px;
  background: #fff;
  margin-left: 100px;
}

.activedModal h3 {
  font-size: 24px;
  font-weight: 600;
  margin-top: 82px;
}

.activedModal form {
  margin-top: 30px;
}

.activedModal label {
  font-size: 14px;
  font-weight: 500;
}

.activedModal input {
  width: 350px;
  height: 46px;
  font-size: 18px;
  border-radius: 8px;
  border: 0.5px solid #787878;
  padding-left: 40px;
  margin-top: 11px;
}

.InputIconPeople {
  background: url(./Images/carbon_user-filled.png) no-repeat center left 11px;
}

.InputIconTel {
  background: url(./Images/clarity_phone-handset-solid.png) no-repeat center
    left 11px;
}

.activedModal button {
  margin-top: 32px;
}
